html{
  font-size: 62.5%;
}
body {
  background-color: #cd353b;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    width: 100vw;
    color:white;


}
.App {
    padding: 2rem;
    display: flex;
    align-items: center;
    flex-direction: column;
     justify-content: center;
    }

.title {
  text-align: center;
  font-size: 3rem;
  position: relative;
}
.h2{
  text-align: center;
  margin-top: -20px;
  margin-bottom: 20px;
  font-size: 1.6rem;
}



.subtitle {
  border-left: 0.8rem solid #fff;
  padding-left: 1rem;
  color: #eee;
  font-size: 2.5rem;
  margin-top: 18rem;
  margin-left: -100rem;
}
.grid-item {
  cursor: pointer;
  width: 25rem;
  border-radius: 4px;
  padding: 2rem 2rem 2rem 1rem;
  overflow: auto;
  box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
  vertical-align: middle;
  font-size: 1.6rem;
  line-height: 2.6rem;
}


.grid-item .avatar {
  width: 6rem;
  height: 6rem;
  background-color: #eee;
  display: block;
  border-radius: 50%;
  border: 2px solid #fff;
}
.grid-item .userinfo {
  float: left;
  height: 100%;
}
.grid-item .content {
  color: #333;
 margin-left: 8rem;
  /* text-indent: 2em; */
}
.grid-item .nickname {
  color: #999;
  font-size: 1.5rem;
  text-align: right;
  padding-top: 0.6rem;
}

.grid-item .time {
  color: #aaa;
  font-size: 1.5rem;
  text-align: right;
  padding-top: 0.2rem;
}

.load-more {
  margin-top: 8rem;
  margin-bottom: 8rem;
  text-align: center;
  margin-left: 20rem;
}

#hot-comment-list
{
