  #hot-comment-list {
    width: 60rem;
  }
}
@media screen and (max-width: 767.98px) {
  #qq-item {
    width: 60rem;
  };
  .grid-item{
    width: 18rem;
    font-size: 1.4rem;
  };
  iframe{
    width:40rem;
    height: 30rem;
  };



}


.say {
  margin-top: 8rem;
  color: white;
  width: 75%;
  margin-bottom: 23rem;
  line-height: 5rem;
  font-weight: 500;
  font-size: 2rem;

}

.say.thanks{
  margin-top: 0px;
  margin-bottom: 100px;
}
